// Metodi joka pivitt singleUser -staten postauksen kirjoittaneeksi kyttjksi
function handleProfileClick({ setSingleUser, author_id }) {
    if (setSingleUser !== undefined) {
        userServices.getById(author_id)
            .then(user => {
                setSingleUser(user)
            })
    }
}

